* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
}

body {
    font-family: "Poiret One";
    background-color: #6ccfff;
}

.create-round-wrapper {
    margin-bottom: 15px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.create-round__input:nth-child(1) {
    background-color: #bababa;
    color: #2b2b2b;
}
.create-round__input {
    background-color: #2b2b2b;
    border: none;
    border-radius: 100px;
    padding: 10px;
    color: whitesmoke;
    margin-right: 5px;
    margin-bottom: 5px;
}

.create-round__add-btn {
    width: 35px;
    height: 35px;
    color: whitesmoke;
    background-color: #2b2b2b;
    border: none;
    border-radius: 50px;
    font-size: 20px;
    cursor: pointer;
    box-shadow: 0 3px 0 #202020;
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
}
.create-round__add-round-btn {
    background-color: #bababa;
    color: #2b2b2b;
    box-shadow: 0 3px 0 #868686;
}
.create-round__add-btn:hover {
    box-shadow: 0 2px 0 #202020;
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px);
}
.create-round__add-round-btn:hover {
    box-shadow: 0 2px 0 #868686;
}
.create-round__add-btn:focus {
    background-color: #66af50;
    box-shadow: 0 3px 0 #478342;
}
.create-window {
    position: fixed;
    top: 50px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 95vw;
    height: 85vh;
    background-color: #3c3f41f5;
    padding: 50px;
    border-radius: 5px;
}

.level-create__head {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    padding: 20px 0;
    width: 100%;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.level-create__head input {
    width: 45%;
    padding: 10px;
    border: 1px solid #b3b3b3;
    border-radius: 10px;
    background-color: #2b2b2b;
    color: whitesmoke;
    min-width: 200px;
    margin-bottom: 5px;
}

.create-window__create-button {
    position: absolute;
    bottom: 30px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 200px;
    height: 40px;
    border: none;
    border-radius: 8px;
    font-weight: bold;
    cursor: pointer;
    box-shadow: 0 6px 0 #b4b4b4;
}
.create-window__create-button:hover {
    -webkit-transform: translateX(-50%) translateY(2px);
            transform: translateX(-50%) translateY(2px);
    box-shadow: 0 4px 0 #b4b4b4;
}

.create-window__create-button:focus {
    background-color: #66af50;
    box-shadow: 0 6px 0 #478342;
}

.level-creater__exit-btn {
    position: absolute;
    top: 25px;
    right: 25px;
    color: whitesmoke;
    background-color: transparent;
    border: none;
    font-size: 30px;
    cursor: pointer;
    -webkit-transition: color 0.4s;
    transition: color 0.4s;
}

.level-creater__exit-btn:hover {
    color: #ff7d7d;
}
.PageMain main {
    padding: 50px;
}

.PageMain .LevelsList {
    list-style: none;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.levels-list__add-btn {
    height: 60px;
    width: 60px;
    font-size: 30px;
    background-color: #2b2b2b;
    border: none;
    border-radius: 8px;
    color: whitesmoke;
    cursor: pointer;
    text-shadow: 0 0 3px whitesmoke;
}
header {
    text-align: center;
    padding: 35px 0;
    font-size: 22px;
    background-color: #629fcdd0;
}

.logoMain span {
    color: #5fff30;
    font-size: 20px;
}



.LevelCard {
    width: 100%;
    height: 100px;
    background-color: #2b2b2b;
    color: whitesmoke;
    padding: 15px;cursor: pointer;

    border-radius: 8px;
    font-size: 14px;
    margin-bottom: 20px;
    position: relative;
    -webkit-transition: all 0.8s;
    transition: all 0.8s;
}
.LevelCard__play i {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.LevelCard__play {
    left: 0;
    top: 0;
    font-size: 25px;
    font-weight: bold;
    background-color: #1d1d1d;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    position: absolute;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    -webkit-transition: all 0.8s;
    transition: all 0.8s;
    padding: 0;
    opacity: 0;
    width: 0;
    overflow: hidden;
}
.LevelCard:hover {
    padding-left: 45px;
}
.LevelCard:hover .LevelCard__play {
    width: 40px;
    opacity: 1;
    padding: 0 5px;
}

.LevelCard__name {
    text-shadow: 0 0 3px whitesmoke;
}

.LevelCard__description {
    padding: 15px;
}

.fas {
    position: absolute;
    right: 2%;
    top: 20%;
    font-size: 20px;
}

.score-bar {
    width: 80%;
    height: 3px;
    position: relative;
    background-color: #2b2b2b;
    margin: 0 auto;
    margin-top: 20px;
    border-radius: 5px;
}

.score-bar__fill {
    position: absolute;
    height: inherit;
    background-color: #6cff42;
    -webkit-transition: width 0.5s;
    transition: width 0.5s;
}


.RoundWindow {
    background-color: #3c3f41;
    border-radius: 10px;
    padding: 10px 10px;
    display: -webkit-flex;
    display: flex;
    width: 50vw;
    margin: 100px auto;
    -webkit-flex-direction: column;
            flex-direction: column;
    content: "Answer";
}

.RoundWindow form {
    display: inherit;
    -webkit-flex-direction: inherit;
            flex-direction: inherit;
}

.RoundWindow__text {
    color: white;
    padding: 10px;
    font-size: 25px;
}

.RoundWindow__input {
    margin-top: 10px;
    height: 50px;
    padding-left: 10px;
    font-size: 20px;
    letter-spacing: 2px;
    border: none;
    border-radius: 17px;
}

.RoundWindow__button {
    margin-top: 8px;
    height: 40px;
    background-color: inherit;
    border: 1.7px solid white;
    border-radius: 17px;
    color: white;
    opacity: 0.7;
    font-weight: 400;
    letter-spacing: 3px;
    font-size: 18px;
    -webkit-transition: all 0.7s;
    transition: all 0.7s;
}

.RoundWindow__button_action {
    background-color: #ffc723;
    border: none;
    color: white;
    opacity: 0.8;
}

.RoundWindow__button:hover {
    opacity: 1;
}
